@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-page{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;max-width:100%;height:4em}.page-logo{max-width:15%;display:-webkit-flex;display:flex}.htd-logo{height:90%;width:100%;padding-top:0.8%}form{margin-left:300px;margin-top:5px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}input{border-radius:20px;width:35em;height:3em;padding-right:10%;padding-left:1em;border-color:#2b57a7;font-family:"Manrope", sans-serif}.search-btn{border-radius:20px;width:15em;height:3.4em;border:none;margin-left:20px;background-color:#2b57a7;color:white;font-family:"Manrope", sans-serif}.button-features{padding:3vh;background-color:#2b57a7}.button-features button{margin-left:0;margin-right:2vw;border-radius:50px;height:4vh;width:8vw;border:none;font-family:"Manrope", sans-serif;font-weight:400;font-size:14px}.lds-dual-ring{left:1%;top:8%;height:90vh;width:98vw;z-index:100;border-radius:5px;position:absolute;background-color:rgba(244,240,240,0.3)}.lds-dual-ring:after{position:relative;content:" ";display:block;width:150px;height:150px;margin-left:auto;margin-right:auto;top:40%;border-radius:50%;border:15px solid black;border-color:black transparent black transparent;-webkit-animation:lds-dual-ring 1.2s linear infinite;animation:lds-dual-ring 1.2s linear infinite}@-webkit-keyframes lds-dual-ring{0%{opacity:0.9;-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{opacity:0.9;-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-dual-ring{0%{opacity:0.9;-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{opacity:0.9;-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.ecommerce-logos{display:block;-webkit-justify-content:center;justify-content:center;text-align:center}.ecommerce-logos .home-message{font-family:"Manrope", sans-serif;font-weight:800;font-size:2.5em;margin-left:25%;margin-right:25%;margin-top:4%}.ecommerce-logos .store-logos{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.ecommerce-logos img{margin:10vh 5vw 0vh;border-radius:50px}.ecommerce-logos .amazon-logo{max-width:25vw;max-height:30vh}.ecommerce-logos .virgin-logo{max-width:25vw;max-height:30vh}.ecommerce-logos .axiom-logo{max-width:35vw;max-height:40vh;margin-top:0px}.item-data{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.amazon-data{max-height:800px;overflow-y:scroll;width:33%;margin:0;overflow-x:hidden}.amazon-data .amazon-item{min-height:500px;overflow-y:visible;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.amazon-data .amazon-item .img-container{width:300px;height:300px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.amazon-data .amazon-item .img-container img{max-width:100%;max-height:100%}.amazon-data p{width:20vw;font-family:"Manrope", sans-serif;font-weight:bold;font-size:x-large}.virgin-data{max-height:800px;overflow-y:scroll;width:33%;margin:0 5%;overflow-x:hidden}.virgin-data .virgin-item{min-height:500px;overflow-y:visible;width:100%}.virgin-data p{width:20vw;font-family:"Manrope", sans-serif;font-weight:bold;font-size:x-large}.axiom-data{max-height:800px;overflow-y:scroll;width:33%;margin:0 5%;overflow-x:hidden}.axiom-data .axiom-item{min-height:400px;overflow-y:visible;width:100%}.axiom-data p{width:20vw;font-family:"Manrope", sans-serif;font-weight:bold;font-size:x-large}a{text-decoration:none}a:hover{text-decoration:underline}h2{width:20vw;font-family:"Manrope", sans-serif;font-weight:bold;font-size:15px;color:black}.search-for{margin-left:2vw}::-webkit-scrollbar{width:12px}::-webkit-scrollbar-track{-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:10px}::-webkit-scrollbar-thumb{border-radius:10px;background:#2b57a7;-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.5)}

